	exports.push([module.id, ".upload-input[_v-90c96bbc]{width:285px}.file-table[_v-90c96bbc]{margin-top:35px}", "", {"version":3,"sources":["/./src/components/deploy_manage/app_board/Upload.vue.style"],"names":[],"mappings":"AA6IA,2BACA,WAAA,CACA,AAEA,yBACA,eAAA,CACA","file":"Upload.vue","sourcesContent":["<template>\r\n    <modal :show.sync=\"uploadModal\" effect=\"fade\" width=\"550px\">\r\n        <div slot=\"modal-header\" class=\"modal-header\">\r\n            <h4 class=\"modal-title\">上传</h4>\r\n        </div>\r\n        <div slot=\"modal-body\" class=\"modal-body\">\r\n            <form id=\"file_form\" class=\"form-inline text-center\">\r\n                <label>选择文件：</label>\r\n                <input type=\"text\" class=\"form-control upload-input\" onfocus=\"this.blur()\" v-model=\"road\">\r\n                <input id=\"file\" type=\"file\" name=\"file\" v-show=\"false\" @change=\"changeFn\">\r\n                <button type=\"button\" class=\"btn btn-default\" @click=\"findFile\">浏览</button>\r\n                <button type=\"button\" class=\"btn btn-default\" @click=\"uploadFile\">上传</button>\r\n            </form>\r\n            <table class=\"table table-bordered file-table\">\r\n                <thead>\r\n                    <tr>\r\n                        <th>上传人</th>\r\n                        <th>文件名</th>\r\n                        <th>上传时间</th>\r\n                        <th>hash值</th>\r\n                    </tr>\r\n                </thead>\r\n                <tbody>\r\n                    <tr v-show=\"hash !== ''\">\r\n                        <td v-text=\"user\" :title=\"user\"></td>\r\n                        <td v-text=\"filename\" :title=\"filename\"></td>\r\n                        <td v-text=\"time\" :title=\"time\"></td>\r\n                        <td v-text=\"hash\" :title=\"hash\"></td>\r\n                    </tr>\r\n                    <tr class=\"text-center\" v-show=\"hash === ''\">\r\n                        <td colspan=\"4\">暂无数据</td>\r\n                    </tr>\r\n                </tbody>\r\n            </table>\r\n        </div>\r\n        <div slot=\"modal-footer\" class=\"modal-footer\">\r\n            <button type=\"button\" class=\"btn btn-default\" @click='uploadModal = false'>取消</button>\r\n        </div>\r\n    </modal>\r\n</template>\r\n\r\n<script>\r\nimport { modal } from 'vue-strap'\r\n\r\nexport default {\r\n    data () {\r\n        return {\r\n            uploadModal: false,\r\n            idNum: null,\r\n            road: '',\r\n            user: '',\r\n            filename: '',\r\n            time: '',\r\n            hash: ''\r\n        }\r\n    },\r\n    methods: {\r\n\r\n        // 浏览文件\r\n        findFile () {\r\n            $('#file').trigger('click')\r\n        },\r\n\r\n        // 路径捕获\r\n        changeFn () {\r\n            this.road = $('#file').val()\r\n        },\r\n\r\n        // 上传文件\r\n        uploadFile () {\r\n\r\n            if (this.road !== '') {\r\n                let _this = this,\r\n                    formData = new FormData($('#file_form')[0])\r\n\r\n                formData.append('id', _this.idNum)\r\n\r\n                $.ajax({\r\n                        url: '/package_file_upload/',\r\n                        type: 'POST',\r\n                        processData: false,\r\n                        contentType: false,\r\n                        dataType: 'JSON',\r\n                        data: formData\r\n                    })\r\n                    .then(function (data) {\r\n                        if (data.result === 1) {\r\n                            _this.road = ''\r\n\r\n                            _this.getList()\r\n\r\n                            _this.$dispatch('show-success')\r\n                        } else {\r\n                            _this.$dispatch('show-error')\r\n                        }\r\n                    })\r\n            }    \r\n        },\r\n\r\n        // 获取上传记录\r\n        getList () {\r\n            this.$http({\r\n                url: '/package_file_view/',\r\n                method: 'POST',\r\n                data: {\r\n                    id: this.idNum\r\n                }\r\n            })\r\n            .then((response) => {\r\n                if (response.data.result === 1) {\r\n                    this.user = response.data.user\r\n                    this.filename = response.data.file_name\r\n                    this.time = response.data.upload_time\r\n                    this.hash = response.data.hash\r\n                } else if (response.data.result === -1) {\r\n                    this.user = ''\r\n                    this.filename = ''\r\n                    this.time = ''\r\n                    this.hash = ''\r\n                } else {\r\n                    this.$dispatch('show-error')\r\n                }\r\n            })\r\n        }\r\n    },\r\n    components: {\r\n        modal\r\n    },\r\n    events: {\r\n        'showUpload' (param) {\r\n            this.idNum = param\r\n\r\n            this.uploadModal = true\r\n\r\n            this.getList()\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.upload-input {\r\n    width: 285px;\r\n}\r\n\r\n.file-table {\r\n    margin-top: 35px;\r\n}\r\n</style>"],"sourceRoot":"webpack://"}]);
	
